import { STATIC_SEGMENTS } from "./staticSegments";

const API_BASE = process.env.NEXT_PUBLIC_API_BASE || "http://localhost:8000";

async function request(path, options = {}) {
    const url = `${API_BASE}${path}`;
    const res = await fetch(url, {
        ...options,
        headers: {
            "Content-Type": "application/json",
            ...(options.headers || {}),
        },
        cache: "no-store",
    });
    if (!res.ok) {
        const text = await res.text();
        throw new Error(`API ${res.status} ${res.statusText}: ${text}`);
    }
    return res.json();
}

export async function fetchSegments() {
    // Use the provided static segment list to avoid relying on the 2-year dataset
    return STATIC_SEGMENTS;
}

export async function fetchDatathonForecast(road_id, timestamp, current_congestion_pct) {
    return request("/api/datathon/forecast", {
        method: "POST",
        body: JSON.stringify({ road_id, timestamp, current_congestion_pct }),
    });
}

export async function generateMitigation(criticalSegments) {
    return request("/api/generate-mitigation", {
        method: "POST",
        body: JSON.stringify({ critical_segments: criticalSegments }),
    });
}

export async function sendChatMessage(user_message, chat_history, live_context) {
    return request("/api/chat/message", {
        method: "POST",
        body: JSON.stringify({ user_message, chat_history, live_context }),
    });
}

export async function fetchHealth() {
    return request("/api/health");
}

export async function searchPredictions() {
    // Serve synthetic live readings so frontend works without the 2-year dataset
    const now = Date.now();
    return STATIC_SEGMENTS.map((seg, idx) => {
        const base = 30 + (idx * 13) % 50; // spread values without full randomness for stability
        const congestion_pct = Math.min(95, Math.max(10, base + (Math.random() * 25 - 12)));
        const band = congestion_pct >= 80 ? "severe" : congestion_pct >= 60 ? "heavy" : "moderate";
        return {
            road_id: seg.road_id,
            road_name: seg.road_name,
            segment_name: seg.segment_name,
            congestion_pct,
            congestion_band: band,
            speed_kmph: 60 - congestion_pct * 0.4,
            timestamp: new Date(now - Math.random() * 5 * 60 * 1000).toISOString(),
        };
    });
}
